  sam.media = media = {
    buttonId: '#banner-media',
    adUrl: '#img',
    adImgId: '#img_id',
    adName: '#title',
    adDesc: '#description',
    adLink: '#link',
    adAlt: '#alt',
    adFallback: '#swf_fallback',
    btnFallback: '#fallback-code',

    init: function () {
      $(this.buttonId).on('click', this.openMediaDialog);
      $(this.btnFallback).on('click', this.openMediaDialog);
    },

    openMediaDialog: function (e) {
      e.preventDefault();
      const btnId = '#' + e.currentTarget.id;

      if (this._frame) {
        this._frame.open();
        return;
      }

      this._frame = media.frame = wp.media({
        title: mediaTexts.title,
        button: {
          text: mediaTexts.button
        },
        multiple: false,
        library: {
          type: 'image'
        }
      });

      this._frame.state('library').on('select', function () {
        const attachment = this.get('selection').single();
        media.handleMediaAttachment(attachment, btnId);
      });

      this._frame.open();
    },

    handleMediaAttachment: function (a, id) {
      const attachment = a.toJSON();
      if(id === this.buttonId) {
        $(this.adUrl).val(attachment.url);
        $(this.adImgId).val(attachment.id);
        if ('' === $(this.adName).val() && '' !== attachment.title) $(this.adName).val(attachment.title);
        if ('' === $(this.adDesc).val() && '' !== attachment.caption) $(this.adDesc).val(attachment.caption);
        if ('' === $(this.adAlt).val() && '' !== attachment.alt) $(this.adAlt).val(attachment.alt);
      }
      else if(id === this.btnFallback) {
        const
          target = $(this.adLink).val(),
          anchor = (target !== '') ? '<a href="' + target + '">' : '',
          image = '<img src="' + attachment.url + '">',
          anchorE = (target !== '') ? '</a>' : '',
          code = anchor + image + anchorE;
        $(this.adFallback).val(code);
      }
    }
  };

  function getTypes(x) {
    let out = [];
    pageTypes.forEach((e, i) => {
      if (x & e) out.push(i);
    });
    return out;
  }

  function setTypes(x, types) {
    let out = types;
    for(let i = 0; i < x.length; i++) {
      out |= parseInt(x[i].id, 10);
    }
    return out;
  }

  function removeTypes(x, types, parent) {
    let out = types;
    const p = parseInt(parent, 10);
    for(let i = 0; i < x.length; i++) {
      out = out & ~ parseInt(x[i].id, 10);
    }
    out = out & ~ p;
    return out;
  }

